/**
 * @description  class
 */

import $, { DomElement } from '../../utils/dom-core'
import DropListMenu from './DropListMenu'
import { EMPTY_FN } from '../../utils/const'

export type DropListItem = {
    $elem: DomElement
    value: string
}

// droplist 
export type DropListConf = {
    title: string
    list: DropListItem[]
    type: string // 'list'  'inline-block' 
    clickHandler: (value: DropListItem['value']) => void
    width: number | 100
}

class DropList {
    private menu: DropListMenu
    private conf: DropListConf
    private $container: DomElement
    private rendered: boolean
    private _show: boolean

    public hideTimeoutId: number

    constructor(menu: DropListMenu, conf: DropListConf) {
        this.hideTimeoutId = 0
        this.menu = menu
        this.conf = conf

        // 
        const $container = $('<div class="w-e-droplist"></div>')

        // 
        const $title = $(`<p>${conf.title}</p>`)
        $title.addClass('w-e-dp-title')
        $container.append($title)

        // 
        const list = conf.list || []
        const type = conf.type || 'list'
        // item 
        const clickHandler = conf.clickHandler || EMPTY_FN

        //  DOM 
        const $list = $('<ul class="' + (type === 'list' ? 'w-e-list' : 'w-e-block') + '"></ul>')
        list.forEach(item => {
            const $elem = item.$elem

            const value = item.value
            const $li = $('<li class="w-e-item"></li>')
            if ($elem) {
                $li.append($elem)
                $list.append($li)
                $li.on('click', (e: Event) => {
                    clickHandler(value)

                    // 
                    e.stopPropagation()

                    // item  list
                    this.hideTimeoutId = window.setTimeout(() => {
                        this.hide()
                    })
                })
            }
        })
        $container.append($list)

        // 
        $container.on('mouseleave', () => {
            this.hideTimeoutId = window.setTimeout(() => {
                this.hide()
            })
        })

        // 
        this.$container = $container
        this.rendered = false
        this._show = false
    }

    /**
     *  DropList
     */
    public show() {
        if (this.hideTimeoutId) {
            // 
            clearTimeout(this.hideTimeoutId)
        }

        const menu = this.menu
        const $menuELem = menu.$elem
        const $container = this.$container
        if (this._show) {
            return
        }
        if (this.rendered) {
            // 
            $container.show()
        } else {
            //  DOM 
            const menuHeight = $menuELem.getBoundingClientRect().height || 0
            const width = this.conf.width || 100 //  100
            $container.css('margin-top', menuHeight + 'px').css('width', width + 'px')

            //  DOM
            $menuELem.append($container)
            this.rendered = true
        }

        // 
        this._show = true
    }

    /**
     *  DropList
     */
    public hide() {
        const $container = this.$container
        if (!this._show) {
            return
        }
        // 
        $container.hide()
        this._show = false
    }

    public get isShow() {
        return this._show
    }
}

export default DropList
